import React from 'react';
import '../../../App.css';
import SongCard from '../SongComponent/SongCard';

export default function MultiCard({array,player,update}) {
    
    return (
        <div className='multi_card' onClick = {()=>{player(); update()}}>
               { array.map((song) =>{           
                    return(           
                        <SongCard  key = {array.indexOf[song]} data = {song}/>
                    )
               })
           }
        </div>
    )
}
